import React, { Fragment } from 'react';
import Classes from './Header.module.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link, withRouter, NavLink } from "react-router-dom";
import '../Active.css'

const Header = (props) => {
    return (
        <Fragment>
            <header className={Classes.Header}>
                <Navbar collapseOnSelect expand="lg" variant="dark" sticky="top">
                    <Navbar.Brand>
                        <Link to={{ pathname: props.match.url }} className='mr-3 text-decoration-none'>Routing App</Link>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className={[Classes.NavBar, "mr-auto"].join(' ')}>
                            <NavLink to={{ pathname: props.match.url, }}
                                className='mr-3' exact>Home</NavLink>
                            <NavLink to={{ pathname: props.match.url + 'users' }} className='mr-3'>Users</NavLink>
                            <NavLink to={{ pathname: props.match.url + 'features' }} className='mr-3'>Features</NavLink>
                            <NavLink to={{ pathname: props.match.url + 'pricing' }} className='mr-3'>Pricing</NavLink>
                            <NavLink to={{ pathname: props.match.url + 'feedback' }} className='mr-3'>Feedback</NavLink>
                        </Nav>
                        <Nav className={Classes.NavBar}>
                            <NavLink to={{ pathname: props.match.url + 'login' }} className='mr-3'>LogIn</NavLink>
                            <NavLink to={{ pathname: props.match.url + 'register' }} className='mr-3'>Register</NavLink>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </header>
        </Fragment>
    );
};

export default withRouter(Header);