import React, { Fragment } from 'react';
import '../Active.css';
import Classes from './ChildNavs.module.css'
import { NavLink, Switch, Route, useRouteMatch } from 'react-router-dom';

const ChildNavs = (props) => {
    const { path, url } = useRouteMatch();

    return (
        <Fragment>
            <div className={Classes.ChildNavs}>
                <h3>Features</h3>
                <NavLink to={`${url}/flexiblity`}>Flexiblity</NavLink><br />
                <NavLink to={`${url}/durability`}>Durability</NavLink><br />
                <NavLink to={`${url}/time-saving`}>Time-Saving</NavLink>
                <Switch>
                    <div className='mt-3 font-weight-bold'>
                        <Route path={`${path}/flexiblity`}>
                            <h4 className='mb-4'>Flexiblity</h4>
                        </Route>
                        <Route path={`${path}/durability`}>
                            <h4 className='mb-4'>Durability</h4>
                        </Route>
                        <Route path={`${path}/time-saving`}>
                            <h4 className='mb-4'>Time-Saving</h4>
                        </Route>
                    </div>
                </Switch>
            </div>
        </Fragment>
    );
};

export default ChildNavs;